.partners {
    margin: 1.2em;
}

.partners > h2 {
    margin-bottom: .15em;
}

.p-c {
    flex-wrap: wrap;
}
  
.hs {
    display: grid;
    grid-gap: 35px;
    grid-template-columns: 1px 250px 250px 250px 250px 250px 250px 1px;
    overflow-x: scroll;
    scroll-snap-type: x proximity;
    padding: 2em 0 2.2em 0;
}

.hs::-webkit-scrollbar {
    display: none;
}
  
.hs:before,
.hs:after {
    content: '';
}

.arrow-right, .arrow-left {
    position: absolute;
    top: 50%;
    width: 60px;
    opacity: .5;
    z-index: 25;
}

.arrow-left:hover,
.arrow-right:hover {
    opacity: 1;
}

.arrow-right {
    right: 0;
    animation: arrow-right 2s linear infinite;
}

.arrow-left {
    left: 0;
    animation: arrow-left 2s linear infinite;
}

@keyframes arrow-right {
    0% {
        transform: translateX(0);
    }
    50% {
        transform: translateX(-5px);
    }
    100% {
        transform: translateX(0);
    }
}

@keyframes arrow-left {
    0% {
        transform: translateX(0);
    }
    50% {
        transform: translateX(5px);
    }
    100% {
        transform: translateX(0);
    }
}